import React from 'react'
import { Layout, Menu} from 'antd';
import mainlogo from '../images/scholar.png'
import {
  DesktopOutlined,
  PieChartOutlined,
  FileOutlined,
  AppstoreOutlined,
  UserOutlined,
} from '@ant-design/icons';
import "../index.css";
import "antd/dist/antd.css"

const { Sider } = Layout;
const { SubMenu } = Menu;

class SideBar extends React.Component {
  state = {
    collapsed: false,
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  render() {
    const { collapsed } = this.state;
    return (
        <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse}>
          <div className="logo" style={{padding:"0 25%"}}>
            <span>
              {collapsed && <img src={mainlogo} alt="" style={{height:"45px",}}/>}
              {!collapsed && <h1 style={{color:"white"}}>TMS</h1>}
            </span>
          </div>
          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
            <SubMenu key="sub1" icon={<UserOutlined />} title="User">
              <Menu.Item key="1">Profile</Menu.Item>
              <Menu.Item key="2">Log Out</Menu.Item>
            </SubMenu>
            <Menu.Item key="3" icon={<DesktopOutlined />}>
              Courses
            </Menu.Item>
            <Menu.Item key="4" icon={<PieChartOutlined />}>
              Progress Report
            </Menu.Item>
            <SubMenu key="sub2" icon={<FileOutlined />} title="Thesis">
              <Menu.Item key="5">New</Menu.Item>
              <Menu.Item key="6">Pending</Menu.Item>
              <Menu.Item key="7">Completed</Menu.Item>
            </SubMenu>
            <SubMenu key="sub3" icon={<AppstoreOutlined />} title="More">
              <Menu.Item key="8">Forms</Menu.Item>
              <Menu.Item key="9">XYZ</Menu.Item>
            </SubMenu>
          </Menu>
        </Sider>
    );
  }
}

export default SideBar;